/* desktop and mobile common layout */
*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  color: white;
}

body{
  overflow: visible;
  background-color: rgb(32, 32, 32);
  z-index: -2;
}

.bunch-of-logos{
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  -webkit-flex-direction:row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  -webkit-flex-wrap:wrap;
  width: 100%;
  /* border: 5px solid orange; */
  text-align: center;
}

.bunch-of-logos > .one-logo:hover> img{
  width:105%;
  transition: 500ms;
}

.bunch-of-logos > .one-logo > img{
  width: 100%;
  object-fit: cover;
  clip-path: circle(50%);
}

.deselect{
  opacity: 0.6;
  filter: grayscale(100%) invert(100%);
  /* transform: scale(0.6); */
}

.stream-xAxis{
  opacity: 0.5;
}

.stream-yAxis{
  opacity: 0.5;
}
.stream-yAxis > .domain{
  stroke-width: 0;
}

.tooltip-yAxis-1>.domain{
  stroke-width: 0;
}
.tooltip-yAxis-1>.tick>line{
  opacity: 1;
}
.tooltip-yAxis-1>.tick>text{
  font-size: calc(8px + 0.5vw);
  color: rgba(34, 34, 94, 0.959);
  font-weight: 900;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  /* transform: translate(0,-0.6em); */
}

.tooltip-yAxis-1>.tick:first-child>line{
  opacity: 0.8;
  /* stroke-color:red */

}
/* .tooltip-yAxis-1>.tick:first-child>text{ */
  /* font-size: 1em; */
  /* transform: translate(0,-0.6em); */
/* } */

.floaterToolTip{
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  -webkit-flex-direction:column;
  flex-wrap: nowrap;
  justify-content: center;
  z-index: 3;
  background-color: rgb(32, 32, 32);
  opacity: 0.8;
  max-width: 200px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.floaterToolTip>.person-name{
  font-weight: 600;
  font-size: calc(10px + 0.3vw);
  background-color: black;
}
.floaterToolTip>.job-title{
  text-transform: uppercase;
  font-weight: 500;
  font-size: calc(8px + 0.3vw);
}
.floaterToolTip>.salary-paid{
  font-weight: 500;
  font-size: calc(10px + 0.3vw);
  background-color:black;
}
.floaterToolTip>.taxable-benefits{
  font-weight: 500;
  font-size: calc(10px + 0.3vw);
}

