.streamgraph-wrapper.browser{
  width: 100%;
  min-height: 80%;
  display: flex;
  display: -webkit-flex;
  border:2px solid green;
  flex-basis: 75%;
  flex-direction: column;
  -webkit-flex-direction:column;
  overflow: visible;
  align-items: center;
}

.slider.browser{
  width: 98%;
  flex-basis: 2%;
  -webkit-flex-basis:2%;
  background-color: red;
}

.slider-group>.tick>text{
  font-size:20px;
  fill:white
}

.streamgraph-svg.browser{
  width: 100%;
  height: 100%;
  /* flex-basis: 95%; */
  /* -webkit-flex-basis:95%; */
