  display: -webkit-flex;
  overflow: visible;
  flex-basis: 98%;
}

.side-graph.browserView{
  /* border: 2px solid red; */
  width: 100%;
  margin-top: 4%;
  z-index: 5;
  overflow: visible;
  display: flex;
  display: -webkit-flex;
  flex-basis: 25%;
  -webkit-flex-basis:25%;
  max-height: 30vh;
}

.side-graph-wrapper.browser{
  /* border: 2px solid green; */
