  font-weight: 600;
  text-transform: uppercase;
  font-size: calc(10px+0.5vw);
}

.side-graph-svg.browser{
  width:100%;
  overflow: visible;
}

.tooltip.browserView{
  background-color: rgba(218, 218, 218, 0.63);
  width: 20%;
  min-width: 300px;
  min-height: 150px;
  height:20%;
  position: absolute;
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  -webkit-flex-direction:column;
  flex-wrap: nowrap;
}  

.tooltip-header.browser{
  width: 100%;
  display: flex;
  display: -webkit-flex;
  justify-content: space-between;
  /* border:1px solid yellow; */
