}
.tooltip-wrapper.browser{
  /* border:2px solid red; */
  display: flex;
  display: -webkit-flex;
}  

.tooltip-svg.browser{
  display: flex;
  display: -webkit-flex;
  /* padding:2px 5px 0px 5px; */
  background-color:rgba(223, 223, 223, 0.925);
  height:100%;
  width: 100%;
}  

.tooltip-footer.browser{
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  border:1px solid yellow;
  font-size: calc(8px + .4vw);
  font-weight: 600;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.tooltip-footer.browser > h4{
